body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body{background:#f2efe9}ul{list-style-type:none;margin:0;padding:0}a{text-decoration:none;color:#fff}label{display:block;width:100%;margin-bottom:10px;}label input,label textarea{outline-color:rgba(70,70,85,0.9);outline-width:3px;border:1px solid #7e7e7e;margin-top:5px;width:100%}button{border:2px solid #252627;padding:4px 12px;float:right;background:#f2efe9;-webkit-transition-duration:.4s;transition-duration:.4s;font-weight:normal;}button:hover{color:#f2efe9;background:#252627;opacity:.8}.container{max-width:320px;margin:0 auto}.col-4{display:inline-block;box-sizing:border-box;margin-bottom:20px;width:100%}.col-12{max-width:100%}.wrapper{padding:20px}@media (min-width:320px){.container{max-width:90%}}@media (min-width:768px){.container{max-width:85%}.col-4{margin:1%;width:30%}}@media (min-width:1024px){.container{max-width:80%}}
#Header{display:flex;align-items:center;height:60px;background:#252627;}#Header .container{display:flex;justify-content:space-between;width:80%;margin:0 auto}#logo{display:inline-block;line-height:60px;color:#fff;font-size:1.5rem}
li{padding:20px;top:50%;}li a{text-transform:capitalize}.nav{display:none;}.nav--active{display:block;position:fixed;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.8)}.nav__list--active{display:block;position:relative;top:50%;text-align:center;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.nav__trigger{z-index:1;display:block;line-height:60px;color:#fff;cursor:pointer;text-transform:uppercase}@media (min-width:768px){.nav{display:block;}.nav__list{display:block;}.nav__list li{display:inline-block}.nav__trigger{display:none}}
.breadcrumbs{float:right;display:flex;padding:5px;font-size:14px;background:rgba(0,0,0,0.5);text-transform:capitalize;}.breadcrumbs ul{padding:7px}.breadcrumbs li{box-sizing:border-box;display:inline-block;padding:5px;color:#fff;}.breadcrumbs li:after{content:"/";margin-left:10px}.breadcrumbs li:last-child:after{content:"";margin:0}.breadcrumbs li a{color:#ccc;text-decoration:none;-webkit-transition:.5s;transition:.5s;}.breadcrumbs li a:hover{color:#fff}.feature{padding:100px 0;background-image:url("https://picsum.photos/1920/1080/?random");background-size:cover;background-position:50% 50%;background-attachment:fixed}.feature__title{display:inline-block;color:#fff;text-transform:uppercase}
.Blog .post__article{cursor:pointer;border-radius:5px;box-shadow:-9px 10px 10px rgba(0,0,0,0.3);height:200px;overflow:hidden;background:#909cc2;background:-webkit-linear-gradient(right top,transparent 50%,rgba(0,0,0,0.5) 0) 100% 0 no-repeat,-webkit-linear-gradient(225deg,transparent 1.5em,#f7f5fb 0);background:linear-gradient(to left bottom,transparent 50%,rgba(0,0,0,0.5) 0) 100% 0 no-repeat,linear-gradient(-135deg,transparent 1.5em,#f7f5fb 0);background-size:2em 2em,auto;}.Blog .post__article h1{font-size:2rem;height:2.5rem;overflow:hidden}.Blog .post__article h3{height:5%}.Blog .post__article p{height:65%;overflow:hidden}.Blog .post__list{padding:30px 0}@media (min-width:768px){.Blog .post__article{height:300px}}
.Post{display:flow-root;padding-top:50px;text-transform:capitalize;font-size:18px;color:#333}@media (min-width:768px){.Blog .post__article{height:300px}}
.Contact{padding:50px 0;text-transform:capitalize;}.Contact__content{border-radius:6px;box-shadow:-9px 10px 10px rgba(0,0,0,0.3);height:100%;overflow:hidden;background:#909cc2;background:-webkit-linear-gradient(right top,transparent 50%,rgba(0,0,0,0.5) 0) 100% 0 no-repeat,-webkit-linear-gradient(225deg,transparent 1.5em,#f7f5fb 0);background:linear-gradient(to left bottom,transparent 50%,rgba(0,0,0,0.5) 0) 100% 0 no-repeat,linear-gradient(-135deg,transparent 1.5em,#f7f5fb 0);background-size:2em 2em,auto}.Contact a{border:2px solid #000;padding:3px 15px;color:#000;font-size:20px}.Contact textarea{height:18vh}.Contact button{float:right;background-color:#f7f5fb;}.Contact button:hover{color:#f2efe9;background:#252627;opacity:.8}@media (min-width:768px){.Contact{padding:100px 0}}
.Article{padding:100px 0;}.Article a{border:2px solid #252627;padding:3px 15px;color:#252627;font-size:16px;-webkit-transition:.3s;transition:.3s;text-transform:capitalize;font-weight:normal;}.Article a:hover{color:#f2efe9;background-color:#252627;opacity:.8}.Article__content{border-radius:6px;box-shadow:-9px 10px 10px rgba(0,0,0,0.3);height:400px;overflow:hidden;background:#909cc2;background:-webkit-linear-gradient(right top,transparent 50%,rgba(0,0,0,0.5) 0) 100% 0 no-repeat,-webkit-linear-gradient(225deg,transparent 1.5em,#f7f5fb 0);background:linear-gradient(to left bottom,transparent 50%,rgba(0,0,0,0.5) 0) 100% 0 no-repeat,linear-gradient(-135deg,transparent 1.5em,#f7f5fb 0);background-size:2em 2em,auto}@media (min-width:320px){.Article{padding:50px 0}}
footer{text-align:center;font-size:13px;line-height:60px;color:#fff;height:60px;background-color:#252627}
